.jobs-list > ul > li {
    padding-bottom: 30px;
}
.jobs-list > ul > li:last-child{
    padding-bottom: 0;
}
.indi-job-wrapper{
    position: relative;
    display: block;
    border: 1px solid #ECECEC;
    background-color: #fff;
    padding: 25px 20px;
}

.emloyer-info{
    display: flex;
    align-items: flex-start;
    padding-bottom: 10px;
}

.job-info{
    flex: 1;
}

.employer-logo{
    width: 62px;
    min-width: 62px;
    height: 62px;
    margin-right: 10px;
}
.employer-logo img{
    width: 100%;
    height: 100%;
    object-fit: contain;
    object-position: center;
}

.job-position{
    display: block;
    font-size: 24px;
    font-weight: 600;
    color: #080036;
    padding-bottom: 5px;
}

.indi-job-wrapper .job-desc{
    font-size: 16px;
    color: #6E7775;
    padding-bottom: 10px;
}
.job-desc-list{
    padding-bottom: 20px;
}
.job-desc-list > li {
    font-size: 16px;
    color: #6E7775;
    padding-left: 30px;
    padding-bottom: 10px;
    position: relative;
}
.job-desc-list > li:last-child{
    padding-bottom: 0;
}

.job-desc-list > li::before{
    content: '\F285';
    display: inline-block;
    font-family: bootstrap-icons !important;
    font-style: normal;
    font-weight: normal !important;
    font-variant: normal;
    text-transform: none;
    line-height: 1;
    vertical-align: -.125em;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    font-size: 16px;
    position: absolute;
    left: 0;
}

.indi-job-wrapper .bookmark-btn{
    position: absolute;
    right: 15px;
    top: 15px;
}

.job-info-bottom{
    position: relative;
}

/* .job-info-bottom .job-metas{
    position: absolute;
    right: 0;
    top: 1px;
} */


@media screen and (max-width: 767px){
    .employer-logo{
        width: 48px;
        min-width: 48px;
        height: 48px;
    }
    .job-position{
        font-size: 20px;
    }
}
@media screen and (max-width: 576px){
    .job-info-bottom > *{
        margin-bottom: 10px;
    }
    .job-info-bottom > *:last-child{
        margin-bottom: 0;
    }
}


